li:hover {
    font-weight: bolder;

    transition: 0.3s;


}

.menu_active {
    font-weight: bold;
    border-right: 3px solid bisque;
}

li {
    text-decoration: none;
}

.sidebar {
    background-color: purple;
    left: 0;
    padding-left: 0%;
    border-radius: 5px 15px 15px 5px;
    position: relative;
}
.btn-get-started {
    font-weight: 500;
    font-size: 16px;
    letter-spacing: 1px;
    display: inline-block;
    padding: 10px 30px;
    border-radius: 50px;
    transition: 0.5s;
    color: #172e69;
    border: 2px solid #172e69;
    text-decoration: none;
}

.btn-get-started:hover {
    background: #172e69;
    color: white;
    font-weight: 700;
}
.button {
    width: 170px;
    padding-top: 10px;
    padding-bottom: 10px;
    text-align: center;

    font-weight: 200;
    color: black;

    cursor: pointer;
    display: inline-block;
}

.button-1 {
    background-color: transparent;
    border: 3px solid #172e69;
    ;
    border-radius: 50px;
    transition: all .15s ease-in-out;
    color: #172e69;

}

.button-1:hover {
    box-shadow: 0 0 10px 0 #00d7c3 inset, 0 0 20px 2px #00d7c3;
    border: 3px solid #00d7c3;
}
.title {
    text-transform: capitalize;

}
